/**
* 
*/
export function getPointsDistance (p1, p2) {
    return Math.sqrt(Math.pow((p1.x - p2.x), 2) + Math.pow((p1.y - p2.y), 2))
}

/**
 * x
 */
export function getRotate (p1, p2) {
    return Math.atan((p2.y - p1.y) / (p2.x - p1.x)) / Math.PI * 180
}

/**
 * path
 * @augments:w x
 * @augments:h y
 */
export function getCurvePathD (w, h) {
    var start = { x: 0, y: h }
    var end = { x: w, y: h }

    var topCtlPointTranslate = -w * 2.5 / 10 // ...
    var bottomCtlPointTranslate = -w * 2 / 10 // ...

    var path_d = 'M'
        // 
        + start.x
        + ' '
        + start.y
        // 
        + ' Q '
        + ((start.x + end.x) / 2)
        + ' '
        + ((start.y + end.y) / 2 + topCtlPointTranslate)
        + ' '
        // 
        + (end.x - 12)
        + ' '
        + (end.y - 14)
        // 
        + ' L '
        + (end.x - 10)
        + ' '
        + (end.y - 20)
        // 
        + ' L '
        + (end.x)
        + ' '
        + (end.y)
        // 
        + ' L '
        + (end.x - 20)
        + ' '
        + (end.y + 4)
        // 
        + ' L '
        + (end.x - 14)
        + ' '
        + (end.y - 2)
        // 
        + ' Q '
        + ((start.x + end.x) / 2)
        + ' '
        + ((start.y + end.y) / 2 + bottomCtlPointTranslate)
        + ' '
        // 
        + start.x
        + ' '
        + start.y
    return path_d
}


